import Vue from '@/components/base'
import { Component } from 'vue-property-decorator'
import template from './BasicColor.vue'

@Component({
    name: 'BasicColor',
    mixins: [template]
})
export default class BasicColor extends Vue {
    //#region datas ******************************/
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get colors() {
        let assistDesc =
            '功能色代表了明确的信息以及状态，比如成功、出错、失败、提醒、链接等。'
        return [
            this.generateColors(
                'primary',
                '品牌色',
                '品牌色是体现产品特性和传播理念最直观的视觉元素之一。'
            ),
            this.generateColors('success', '功能色-成功', assistDesc),
            this.generateColors('warning', '功能色-警告', assistDesc),
            this.generateColors('danger', '功能色-失败', assistDesc),
            this.generateColors('cyan-blue', '功能色-cyan-blue', assistDesc),
            this.generateColors('purple', '功能色-紫色', assistDesc)
        ]
    }

    // 中性色
    protected get neutralColors() {
        return [
            {
                title: '中性色',
                desc:
                    '中性色主要被大量的应用在界面的文字部分，此外背景、边框、分割线、等场景中也非常常见。产品中性色的定义需要考虑深色背景以及浅色背景的差异，同时结合 WCAG 2.0 标准。',
                colors: [
                    {
                        colorName: '--color-text-primary',
                        textColor: '#FFF',
                        desc:
                            '重要级文字信息/标题信息 -> 模块标题/表头信息/弹窗标题'
                    },
                    {
                        colorName: '--color-text-regular',
                        textColor: '#FFF',
                        desc:
                            '次级文字信息/标题信息 -> 列表内容/表单标题或字段/导航列表/选择器等输入选择文字/icon'
                    },
                    {
                        colorName: '--color-text-secondary',
                        textColor: '#FFF',
                        desc:
                            '辅助文字信息/补充描述 -> 补充描述信息/辅助时间手机号/小标题/禁用失效功能文字/输入框选择器禁用失效文字'
                    },
                    {
                        colorName: '--color-text-placeholder',
                        textColor: '#000',
                        desc: '输入框默认提示文字 -> 关闭开关'
                    },
                    {
                        colorName: '--color-text-disabled',
                        textColor: '#000',
                        desc: '禁用失效文字'
                    },
                    {
                        colorName: '--color-border',
                        textColor: '#000',
                        desc: '组件描边/禁用描边'
                    },
                    {
                        colorName: '--color-border-hover',
                        textColor: '#FFF'
                    },
                    {
                        colorName: '--color-border-active',
                        textColor: '#FFF'
                    },
                    {
                        colorName: '--color-border-line',
                        textColor: '#000',
                        desc: '分割线 -> 分割线/边框线'
                    },
                    {
                        colorName: '--color-bg',
                        textColor: '#000',
                        desc: '全局背景'
                    },
                    {
                        colorName: '--color-bg-disabled',
                        textColor: '#000',
                        desc: '禁用背景色'
                    },
                    {
                        colorName: '--color-bg-content',
                        textColor: '#000',
                        desc: '主内容底色'
                    },
                    {
                        colorName: '--color-bg-side',
                        textColor: '#000',
                        desc: '右侧抽屉-底色'
                    },
                    {
                        colorName: '--color-bg-side-secondary',
                        textColor: '#000',
                        desc: '右侧抽屉-次底色'
                    },
                    {
                        colorName: '--color-bg-list',
                        textColor: '#000',
                        desc: '斑马线及较弱层次底色 -> 列表分割斑马线底色'
                    },
                    {
                        colorName: '--color-bg-component',
                        textColor: '#000',
                        desc: '选择器/输入框默认底色'
                    }
                ]
            }
        ]
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    // 生成颜色
    protected generateColors(name: string, title: string, desc: string) {
        return {
            title,
            desc,
            colors: Array(10)
                .fill(0)
                .map((v, ix) => {
                    let primaryIndex = 5
                    return {
                        colorName:
                            ix === primaryIndex
                                ? `--color-${name}`
                                : `--color-${name}-${ix + 1}`,
                        textColor: ix >= primaryIndex ? '#FFF' : '#000'
                    }
                })
        }
    }
    // 根据 css 变量名称获取变量值
    protected getVarValue(varName: string) {
        let body = document.body

        if (body) {
            return getComputedStyle(body).getPropertyValue(varName)
        }
    }
    //#endregion methods *************************/
}
